.expertise-container {
  background-color: #0d0d0d;
  color: #fff;
  padding: 2rem;
  font-family: "Arial", sans-serif;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid #ffd700;
  display: inline-block;
}

.projects-section,
.research-paper-section {
  margin-bottom: 2rem;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.project-tile {
  background-color: #1a1a1a;
  padding: 1rem;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s, background-color 0.3s;
}

.project-tile:hover {
  transform: scale(1.05);
  background-color: #ffd700;
  color: #0d0d0d;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background-color: #1a1a1a;
  color: #fff;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
  max-width: 600px;
  width: 100%;
}

.modal-content h2 {
  margin-bottom: 1rem;
}

.modal-content p {
  margin-bottom: 1rem;
}

.screenshots img {
  max-width: 100px;
  margin: 0.5rem;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s;
}

.screenshots img:hover {
  transform: scale(1.2);
}

.modal-content button {
  padding: 0.5rem 1rem;
  background-color: #ffd700;
  border: none;
  color: #0d0d0d;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.modal-content button:hover {
  background-color: #e6b800;
}

.image-popout-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1100;
}

.image-popout-content {
  background-color: #1a1a1a;
  padding: 1rem;
  border-radius: 10px;
  max-width: 90%;
  text-align: center;
}

.image-popout-content img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.image-popout-content button {
  padding: 0.5rem 1rem;
  background-color: #ffd700;
  border: none;
  color: #0d0d0d;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.image-popout-content button:hover {
  background-color: #e6b800;
}

.research-paper-content {
  background-color: #1a1a1a;
  padding: 1.5rem;
  border-radius: 10px;
}

@media (min-width: 480px) {
  .font-link {
    font-family: "Hanalei Fill", cursive;
  }
}
